import React from "react";
import "./Article.scss";
import huitre from "../assets/img/huitrecreusebaie.jpg";
import huitre2 from "../assets/img/huitre2.jpg";
import huitreeat from "../assets/img/huitreeats.jpeg";
import bourriche from "../assets/img/bourriche.jpg";
import photo3 from "../assets/img/photo5.jpg";
import photo4 from "../assets/img/photo6.jpeg";
import mer from "../assets/img/photo7.jpeg";
import { Carousel } from "react-responsive-carousel";
import "react-responsive-carousel/lib/styles/carousel.min.css";
import Row from "../Row/Row";
function Article() {
  return (
    <div className="Article">
      <div className="list-price">
        <div className="border">
          <h1>Meilleur vente</h1>
        <Row/>
        </div>

      </div>
      <div className="carrousel">
      <Carousel infiniteLoop autoPlay thumbWidth={150} showStatus='' showThumbs=''>
                <div>
                    <img src={huitre} />
                    <p className="legend">huitre plate classe 2</p>
                </div>
                <div>
                    <img src={huitre2} />
                    <p className="legend">Huitre creuse</p>
                </div>
                <div>
                    <img src={huitreeat} />
                    <p className="legend">Bourriche</p>
                </div>
                <div>
                    <img src={photo3} />
                    <p className="legend">Legend 3</p>
                </div>
                <div>
                    <img src={photo4} />
                    <p className="legend">Legend 3</p>
                </div>
            </Carousel>
            <div className="desc-article-oyester">
          <h1>Huitres de Bretagne</h1>
          <p>
            O sont leves les hutres ? Elles se dclinent en douze grands
            crus bretons, levs dans les viviers sur tout le territoire :
            Cancale, Paimpol, Trguier, Morlaix-Penz, Rade de Brest, les Abers,
            Aven Belon, Etel, Quiberon, Golfe du Morbihan, Penerf, le Croisic,
            sans oublier les hutres sauvages de Rhuys. On distingue les hutres
            creuse, en levage ou sauvages et les hutres plates au petit got
            de noisette.<br></br> Quand les savourer ? Laiteuse ou pas, cest
            une histoire de got ! Consommation les mois en  R  ? Voici une
            petite explication de texte : de mai  aot, en pleine priode de
            reproduction, les hutres produisent de la laitance qui nenlve en
            rien le got et les qualits nutritionnelles de lhutre. Pour
            viter ce phnomne naturel et permettre une dgustation dhutres
            non laiteuses toute lanne, certains ostriculteurs lvent des
            hutres striles (triplodes). Conclusion, on mange des hutres
            toute lanne en Bretagne !
          </p>
          
        </div>
      </div>
     
    </div>
  );
}

export default Article;
