import React from "react";
import Nav  from '../Nav/Nav'
import './Huitres.scss';
import huitre from '../assets/img/huitrecreusebaie.jpg'
function Huitres() {
  return (
    <div>
      <Nav></Nav>
      <div className="desc">
        
        <img src={huitre}></img>
        <div className="desc-oyester">
        <h1>Nos Huitres</h1>
        <div className="desc-article">
          <p>
            <h3>Description physique</h3> Lhutre est un mollusque sans tte, compos
            dune coquille et dun corps mou, et qui possde une bouche, un
            foie, un cur, une gonade et deux reins. Le corps maintient la
            coquille hermtiquement ferme, la protgeant ainsi contre les
            prdateurs. La couleur de la coquille, compose de deux parties
            distinctes et symtriques, varie selon lespce dhutre. Il en
            existe en effet plusieurs varits : hutre franaise, amricaine,
            portugaise Une hutre de 15 ans peut mesurer jusqu 15 cm de
            diamtre. Son lieu de vie Une hutre vit accroche aux rochers, dans
            leau sale (salinit moyenne) de toutes les mers, notamment au
            niveau des estuaires et des zones de mare.
         
          <h3>Sa reproduction</h3> Lhutre se reproduit une ou plusieurs fois en t,
          dans une eau pas trop sale,  une temprature de 21C environ. Sa
          reproduction varie selon la varit de lhutre. Ainsi, lhutre plate
          et arrondie conserve ses ufs  lintrieur de la coquille o se fait
          la fcondation (elle produit de 500 000  1 500 000 ufs), tandis que
          lhutre creuse et allonge produit des ufs quelle expulse dans le
          milieu marin et qui, aprs fcondation, se transforment en larves de
          1/10me  2/10me de millimtre avant de devenir des naissains
          (petites hutres). Il y a lieu de noter que les hutres sont
          hermaphrodites, cest--dire quelles peuvent changer de sexe dune
          saison de reproduction  lautre. On estime que ce changement dpend
          de facteurs externes (alimentation, temprature, salinit...) et de
          leurs hormones. </p>
        </div>
        </div>
      </div>
    </div>
  );
}

export default Huitres;
